import {
  Button,
  HandleInteractionKind,
  HandleType,
  InputGroup,
  Label,
  Menu,
  MenuItem,
  MultiSlider,
  NumericInput,
  Tag,
} from "@blueprintjs/core";
import {
  COMPRESSORS_MAX,
  COMPRESSORS_MIN,
  COOLING_CAPACITY_MAX,
  COOLING_CAPACITY_MIN,
  COOLING_LOCKOUT_MAX,
  COOLING_LOCKOUT_MIN,
  COOLING_PEAK_OFFSET_MAX,
  COOLING_PEAK_OFFSET_MIN,
  EARLIEST_START_MAX,
  EARLIEST_START_MIN,
  ECONOMIZER_SETPOINT_MAX,
  ECONOMIZER_SETPOINT_MIN,
  HEATING_PEAK_OFFSET_MAX,
  HEATING_PEAK_OFFSET_MIN,
  HEAT_PUMP_BACKUP_MAX,
  HEAT_PUMP_BACKUP_MIN,
  HEAT_PUMP_LOCKOUT_MAX,
  HEAT_PUMP_LOCKOUT_MIN,
  LATEST_START_MAX,
  LATEST_START_MIN,
  OPTIMAL_START_DEVIATION_MAX,
  OPTIMAL_START_DEVIATION_MIN,
  OPTIMAL_START_LOCKOUT_MAX,
  OPTIMAL_START_LOCKOUT_MIN,
} from "utils/unit";
import { get, merge } from "lodash";
import { useCallback, useState } from "react";

import { DeepPartial } from "../../utils/types";
import { IUnit } from "controllers/units/action";
import { IconNames } from "@blueprintjs/icons";
import { Popover2 } from "@blueprintjs/popover2";
import { IEnum } from "common/types";
import { ZoneType } from "common";

export function Unit(props: {
  unit: IUnit;
  editing: DeepPartial<IUnit> | null;
  handleChange: (field: string, unit?: DeepPartial<IUnit> | null) => (value: any) => void;
  hidden?: Array<string>;
}) {
  const { hidden, unit, editing, handleChange } = props;

  const [temp, setTemp] = useState({} as any);

  const getValue = useCallback((field: string) => get(editing, field, get(unit, field)), [editing, unit]);

  const renderNumeric = (
    props: any,
    label: string,
    min: number,
    max: number,
    path: string,
    element?: any,
    fractions?: boolean
  ) => {
    let value = temp[path];
    if (value === undefined) {
      value = getValue(path);
    }
    return (
      <Label>
        <b>{label}</b>
        <NumericInput
          {...props}
          step={fractions ? 0.5 : 1}
          {...(fractions && { minorStepSize: 0.0001 })}
          min={min}
          max={max}
          value={value}
          onValueChange={(v, s) => {
            if (fractions) {
              setTemp(merge({}, temp, { [path]: s }));
            }
            handleChange(path, editing)(v);
          }}
          rightElement={element}
          clampValueOnBlur
        />
      </Label>
    );
  };

  const renderTemperature = (props: any, label: string, min: number, max: number, step: number, path: string) => {
    return (
      <Label>
        <b>{label}</b>
        <MultiSlider
          {...props}
          min={min}
          max={max}
          stepSize={0.5}
          labelStepSize={step}
          labelRenderer={(v, o) => (o?.isHandleTooltip || (v > min && v < max) ? `${v}º\xa0F` : "")}
        >
          <MultiSlider.Handle
            type={HandleType.FULL}
            interactionKind={HandleInteractionKind.LOCK}
            value={getValue(path)}
            onChange={(v) => {
              handleChange(path, editing)(v);
            }}
          />
        </MultiSlider>
      </Label>
    );
  };

  const renderDuration = (props: any, label: string, min: number, max: number, step: number, path: string) => {
    return (
      <Label>
        <b>{label}</b>
        <MultiSlider
          {...props}
          min={min}
          max={max}
          stepSize={5}
          labelStepSize={step}
          labelRenderer={(v, o) => (o?.isHandleTooltip || (v > min && v < max) ? `${v}\xa0min` : "")}
        >
          <MultiSlider.Handle
            type={HandleType.FULL}
            interactionKind={HandleInteractionKind.LOCK}
            value={getValue(path)}
            onChange={(v) => {
              handleChange(path, editing)(v);
            }}
          />
        </MultiSlider>
      </Label>
    );
  };

  const renderSelect = (
    props: any,
    label: string,
    values: Array<IEnum<any> | { name: any; label: string }>,
    path: string,
    transform?: (v: string) => any
  ) => {
    return (
      <Label>
        <b>{label}</b>
        <Popover2
          content={
            <Menu>
              {values?.map((value) => (
                <MenuItem
                  key={value.label}
                  text={value.label}
                  onClick={() => handleChange(path, editing)(value.name)}
                />
              ))}
            </Menu>
          }
          placement="bottom-start"
        >
          <Button {...props} rightIcon={IconNames.CARET_DOWN} minimal>
            {transform ? transform(getValue(path)) : getValue(path)}
          </Button>
        </Popover2>
      </Label>
    );
  };

  // const renderCheckbox = (
  //   props: any,
  //   label: string,
  //   path: string,
  //   description: (value: boolean) => string,
  //   reversed?: boolean
  // ) => {
  //   const value = getValue(path);
  //   return (
  //     <Label>
  //       <b>{label}</b>
  //       <Checkbox
  //         {...props}
  //         label={description(value)}
  //         checked={reversed ? !value : value}
  //         onClick={() => handleChange(path, unit)(reversed ? value : !value)}
  //       />
  //     </Label>
  //   );
  // };

  return (
    <>
      <div className="row" hidden={hidden?.includes("label")}>
        <h3> </h3>
        <div className="unit">
          <Label>
            <b>Unit Label</b>
            <InputGroup
              type="text"
              value={getValue(`label`)}
              onChange={(e) => {
                handleChange(`label`, editing)(e.target.value);
              }}
            />
          </Label>
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("peakLoadExclude")}>
        <div className="select">
          {renderSelect(
            {},
            "Participate in Grid Services",
            [
              { name: false, label: "Yes" },
              { name: true, label: "No" },
            ],
            `peakLoadExclude`,
            (v) => (v ? "No" : "Yes")
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("coolingPeakOffset")}>
        <div className="unit">
          {renderTemperature(
            getValue("peakLoadExclude") ? { disabled: true } : {},
            "Cooling Offset During Grid Services",
            COOLING_PEAK_OFFSET_MIN,
            COOLING_PEAK_OFFSET_MAX,
            1,
            `coolingPeakOffset`
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("heatingPeakOffset")}>
        <div className="unit">
          {renderTemperature(
            getValue("peakLoadExclude") ? { disabled: true } : {},
            "Heating Offset During Grid Services",
            HEATING_PEAK_OFFSET_MIN,
            HEATING_PEAK_OFFSET_MAX,
            1,
            `heatingPeakOffset`
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("zoneLocation")}>
        <div className="select">
          {renderSelect(
            {},
            "Zone Location",
            ZoneType.values.filter((v) => v.type === "location"),
            `zoneLocation`,
            (v) => ZoneType.parse(v)?.label || v
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("zoneMass")}>
        <div className="select">
          {renderSelect(
            {},
            "Zone Mass",
            ZoneType.values.filter((v) => v.type === "mass"),
            `zoneMass`,
            (v) => ZoneType.parse(v)?.label || v
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("zoneOrientation")}>
        <div className="select">
          {renderSelect(
            {},
            "Zone Orientation",
            ZoneType.values.filter((v) => v.type === "orientation"),
            `zoneOrientation`,
            (v) => ZoneType.parse(v)?.label || v
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("zoneBuilding")}>
        <div className="select">
          {renderSelect(
            {},
            "Zone Type",
            ZoneType.values.filter((v) => v.type === "building"),
            `zoneBuilding`,
            (v) => ZoneType.parse(v)?.label || v
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("coolingCapacity")}>
        <div className="unit">
          {renderNumeric(
            {},
            "Rated Cooling Capacity",
            COOLING_CAPACITY_MIN,
            COOLING_CAPACITY_MAX,
            `coolingCapacity`,
            <Tag minimal>tons</Tag>,
            true
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("compressors")}>
        <div className="unit">
          {renderNumeric({}, "Number of Compressors", COMPRESSORS_MIN, COMPRESSORS_MAX, `compressors`)}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("optimalStartLockout")}>
        <div className="unit">
          {renderTemperature(
            {},
            "Disable Optimal Start when Outdoor Temperatures are below",
            OPTIMAL_START_LOCKOUT_MIN,
            OPTIMAL_START_LOCKOUT_MAX,
            5,
            `optimalStartLockout`
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("optimalStartDeviation")}>
        <div className="unit">
          {renderTemperature(
            {},
            "Optimal Start Allowable Zone Temperature Deviation",
            OPTIMAL_START_DEVIATION_MIN,
            OPTIMAL_START_DEVIATION_MAX,
            0.5,
            `optimalStartDeviation`
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("earliestStart")}>
        <div className="unit">
          {renderDuration(
            {},
            "Earliest Start Time Before Occupancy",
            EARLIEST_START_MIN,
            EARLIEST_START_MAX,
            30,
            `earliestStart`
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("latestStart")}>
        <div className="unit">
          {renderDuration(
            {},
            "Latest Start Time Before Occupancy",
            LATEST_START_MIN,
            LATEST_START_MAX,
            15,
            `latestStart`
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("heatPump")}>
        <div className="select">
          {renderSelect(
            {},
            "Heat Pump",
            [
              { name: true, label: "Yes" },
              { name: false, label: "No" },
            ],
            `heatPump`,
            (v) => (v ? "Yes" : "No")
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("heatPumpBackup")}>
        <div className="unit">
          {renderNumeric(
            getValue("heatPump") ? {} : { disabled: true },
            "Heat Pump Electric Backup Capacity",
            HEAT_PUMP_BACKUP_MIN,
            HEAT_PUMP_BACKUP_MAX,
            `heatPumpBackup`,
            <Tag minimal>kW</Tag>,
            true
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("heatPumpLockout")}>
        <div className="unit">
          {renderTemperature(
            getValue("heatPump") ? {} : { disabled: true },
            "Heat Pump Auxiliary Heat Lockout",
            HEAT_PUMP_LOCKOUT_MIN,
            HEAT_PUMP_LOCKOUT_MAX,
            8,
            `heatPumpLockout`
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("economizer")}>
        <div className="select">
          {renderSelect(
            {},
            "Economizer",
            [
              { name: true, label: "Yes" },
              { name: false, label: "No" },
            ],
            `economizer`,
            (v) => (v ? "Yes" : "No")
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("economizerSetpoint")}>
        <div className="unit">
          {renderTemperature(
            getValue("economizer") ? {} : { disabled: true },
            "Economizer Switchover Temperature Setpoint",
            ECONOMIZER_SETPOINT_MIN,
            ECONOMIZER_SETPOINT_MAX,
            5,
            `economizerSetpoint`
          )}
        </div>
        <div />
        <div />
      </div>
      <div className="row" hidden={hidden?.includes("coolingLockout")}>
        <div className="unit">
          {renderTemperature(
            getValue("economizer") ? {} : { disabled: true },
            "Compressor Cooling Lockout Temperature",
            COOLING_LOCKOUT_MIN,
            COOLING_LOCKOUT_MAX,
            5,
            `coolingLockout`
          )}
        </div>
        <div />
        <div />
      </div>
    </>
  );
}
